<template>
  <v-expansion-panel>
    <v-expansion-panel-title expand-icon="mdi-chevron-down" collapse-icon="mdi-chevron-up">
      Разрешение
    </v-expansion-panel-title>
    <v-expansion-panel-text>
      <div class="wrapper-resolution">
        <section class="navigation">
          <button :class="{'active': hasOpenSimpleMenu}" @click="openSimpleMenu(true)">Простой</button>
          <button :class="{'active': !hasOpenSimpleMenu}" @click="openSimpleMenu(false)">Продвинутый</button>
        </section>
        <section class="simple" v-if="hasOpenSimpleMenu">
          <div class="form_radio_btn">
            <input id="radio-1" type="radio" name="radio" value="1 : 1" v-model="sizeSimple">
            <label for="radio-1">1 : 1</label>
          </div>

          <div class="form_radio_btn">
            <input id="radio-2" type="radio" name="radio" value="4 : 5" v-model="sizeSimple">
            <label for="radio-2">4 : 5</label>
          </div>

          <div class="form_radio_btn">
            <input id="radio-3" type="radio" name="radio" value="2 : 3" v-model="sizeSimple">
            <label for="radio-3">2 : 3</label>
          </div>

          <div class="form_radio_btn">
            <input id="radio-4" type="radio" name="radio" value="4 : 7" v-model="sizeSimple">
            <label for="radio-4">4 : 7</label>
          </div>

          <div class="form_radio_btn">
            <input id="radio-5" type="radio" name="radio" value="5 : 4" v-model="sizeSimple">
            <label for="radio-5">5 : 4</label>
          </div>

          <div class="form_radio_btn">
            <input id="radio-6" type="radio" name="radio" value="3 : 2" v-model="sizeSimple">
            <label for="radio-6">3 : 2</label>
          </div>

          <div class="form_radio_btn">
            <input id="radio-7" type="radio" name="radio" value="7 : 4" v-model="sizeSimple">
            <label for="radio-7">7 : 4</label>
          </div>
        </section>
        <div v-if="!hasOpenSimpleMenu">
          <v-app>
            <section class="advanced">
              <span>Ширина: {{ dataWidth[valueWidth] }}</span>
              <v-slider
                  v-model="valueWidth"
                  :min="0"
                  :max="dataWidth.length -1"
                  :step="1"
                  color="#36E2FF"
                  :thumb-size="15"
              >
                <template v-slot:thumb-label="valueWidth">
                  {{ dataWidth[valueWidth.modelValue] }}
                </template>
              </v-slider>
            </section>

            <section class="advanced">
              <span>Высота: {{ dataWidth[valueHeight] }}</span>
              <v-slider
                  v-model="valueHeight"
                  :min="0"
                  :max="dataWidth.length -1"
                  :step="1"
                  color="#36E2FF"
                  :thumb-size="15"
              >
                <template v-slot:thumb-label="valueHeight">
                  {{ dataWidth[valueHeight.modelValue] }}
                </template>
              </v-slider>
            </section>
          </v-app>
        </div>
      </div>
    </v-expansion-panel-text>
  </v-expansion-panel>
</template>

<script setup>
import {ref} from "vue";

let hasOpenSimpleMenu = ref(true);
let sizeSimple = ref();
let valueWidth = ref(0);
let valueHeight = ref(0);
let dataWidth = ref([256, 320, 384, 448, 512, 576, 640, 704, 768, 832, 896, 960, 1024])

function openSimpleMenu(isOpen) {
  hasOpenSimpleMenu.value = isOpen;
}

</script>

<style lang="scss">

.v-expansion-panel {
  background-color: #404040E5;
  color: var(--main-light-color);

  .v-expansion-panel-title {
    font-size: 12px;
  }

  .wrapper-resolution {
    display: flex;
    flex-direction: column;
    gap: 20px;

    .navigation {
      display: flex;
      gap: 20px;
      position: relative;
      margin: 10px 0;

      button {
        display: flex;
        align-items: center;
        color: var(--main-light-color);
        font-weight: 600;
        font-size: 13px;
        position: relative;

        &:not(.no-hover)::after {
          content: '';
          position: absolute;
          width: 100%;
          transform: scaleX(0);
          height: 2px;
          bottom: 8px;
          left: 0;
          top: 20px;
          background: linear-gradient(to left, var(--light-blue), var(--light-pink));
          transform-origin: bottom right;
          transition: transform 0.25s ease-out;
          border-radius: 50%;
        }

        &:hover::after {
          transform: scaleX(1);
          transform-origin: bottom left;
        }
      }

      .active {
        &:not(.no-hover)::after {
          content: '';
          position: absolute;
          width: 100%;
          transform: scaleX(1);
          height: 2px;
          bottom: 8px;
          left: 0;
          top: 20px;
          background: linear-gradient(to left, var(--light-blue), var(--light-pink));
          transform-origin: bottom left;
          transition: transform 0.25s ease-out;
          border-radius: 50%;
        }
      }
    }

    .simple {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      gap: 10px;

      .form_radio_btn {
        display: inline-block;

        input[type=radio] {
          display: none;
        }

        label {
          min-width: 50px;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          line-height: 30px;
          font-size: 10px;
          border: 1px solid var(--main-light-color);
          color: var(--main-light-color);
          border-radius: 6px;
          transition: all 0.2s;
        }

        input[type=radio]:checked + label {
          border: 1px solid var(--light-blue);
          color: var(--light-blue)
        }

        label:hover {
          border: 1px solid var(--light-blue);
          color: var(--light-blue)
        }
      }
    }
  }
}


.v-application {
  background-color: #404040E5;
}

.v-application__wrap {
  min-height: 50px;

  .advanced {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 20px;

    span {
      color: var(--main-light-color);
      font-size: 12px;
    }

    .v-input__details {
      display: none;
    }
  }
}


</style>
