<template>
  <v-expansion-panel>
    <v-expansion-panel-title expand-icon="mdi-chevron-down" collapse-icon="mdi-chevron-up">
      Настройки
    </v-expansion-panel-title>
    <v-expansion-panel-text>
      <div class="wrapper-settings">

        <section class="sempler">
          <span>Sempler</span>
          <v-select
              v-model="select"
              :items="items"
              variant="outlined"
              return-object
              persistent-hint
          ></v-select>
        </section>

        <section class="block__check-box">
          <label class="switch">
            <input type="checkbox" @click="skipProcessing = !skipProcessing">
            <span class="slider"></span>
          </label>
          <span>Пропустить предварительную обработку</span>
        </section>

        <section class="block__check-box">
          <label class="switch">
            <input type="checkbox" @click="changeFace = !changeFace">
            <span class="slider"></span>
          </label>
          <span>Исправление лиц</span>
        </section>

        <span class="message">Вы всегда можете масштабировать и улучшить лица после создания изображений</span>
      </div>
    </v-expansion-panel-text>
  </v-expansion-panel>
</template>

<script setup>
import {ref} from "vue";

let items = ['Euler Ancestral', 'Euler', 'LMS', 'DPM-Solver++', 'PLMS', 'DDIM', 'DPM Karras', 'DPM Karras Ancestral'];
let select = ref('Euler Ancestra');
let skipProcessing = ref(false);
let changeFace = ref(false);

</script>

<style lang="scss">

.wrapper-settings {

  .sempler {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 30px;

    span {
      font-size: 12px;
    }

    .v-field__append-inner {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-top: 0;
      height: 20px;
    }

    .v-select .v-field.v-field {
      display: flex;
      align-items: center;
      height: 30px;


    }

    .v-text-field .v-input__details {
      display: none;
    }

    .v-field__input {
      display: flex;
      align-items: center;
      padding: 10px;
    }

    .v-field {
      font-size: 13px;
    }

    .v-select__selection {
      text-align: center;
    }


  }


  .block__check-box {
    display: flex;
    align-items: center;
    margin-bottom: 10px;

    .switch {
      --secondary-container: #313033;
      --primary: var(--light-blue);
      position: relative;
      min-width: 40px;
      min-height: 20px;
      margin-right: 10px;
    }

    input {
      display: none;
      opacity: 0;
      width: 0;
      height: 0;

      &:checked + .slider::before {
        background-color: var(--primary);
      }

      &:checked + .slider {
        background-color: var(--secondary-container);
      }

      &:focus + .slider {
        box-shadow: 0 0 1px var(--secondary-container);
      }

      &:checked + .slider:before {
        transform: translateX(20px);
      }
    }

    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #313033;
      transition: .2s;
      border-radius: 30px;

      &:before {
        position: absolute;
        content: "";
        height: 20px;
        width: 20px;
        border-radius: 20px;
        background-color: #aeaaae;
        transition: .4s;
      }
    }

    span {
      font-size: 11px;
    }
  }

  .message {
    font-size: 11px;
  }
}

.v-menu > .v-overlay__content > .v-list {
  padding: 10px;
}

.v-list-item-title {
  font-size: 13px;
}

.v-list-item--density-default.v-list-item--one-line {
  min-height: 35px;
}

.v-menu > .v-overlay__content > .v-list {
  background-color: #404040E5;
  backdrop-filter: blur(5px);
  color: var(--main-light-color);
}

</style>
