<template>
  <v-expansion-panel>
    <v-expansion-panel-title expand-icon="mdi-chevron-down" collapse-icon="mdi-chevron-up">
      Изображения
    </v-expansion-panel-title>
    <v-expansion-panel-text>
      <div class="wrapper-image">
        <label v-if="!images" class="upload">
          <input type="file" @change="fileUpload($event, 'target')">
          <img class="label-download" src="../../../assets/text-to-image/download-file-icon.svg" alt="">
          <span>Загрузите изображение для создания вариаций</span>
        </label>
        <label v-if="images" class="pre-show">
          <img class="trash" src="../../../assets/text-to-image/trash.svg" alt="" @click="deleteImg">
          <img class="pre-show-img" :src="images.url" alt="">
        </label>
      </div>
    </v-expansion-panel-text>
  </v-expansion-panel>
</template>

<script setup>
import {ref} from "vue";

let images = ref();

function fileUpload(event) {
  const url = URL.createObjectURL(event.target.files[0]);
  images.value = {name: null, url, file: event.target.files[0]};
  console.log(images.value)
}

function deleteImg() {
  images.value = null;
}
</script>

<style scoped lang="scss">
.v-expansion-panel {
  background-color: #404040E5;
  color: var(--main-light-color);

  .v-expansion-panel-title {
    font-size: 12px;
  }

  .v-expansion-panel-text {

    .wrapper-description {
      display: flex;
      flex-direction: column;
      gap: 20px;

      .description {
        p {
        }

        textarea {
          width: 100%;
          resize: none;
          border: none;
          border-bottom: 1px solid var(--main-light-color);
          padding: 10px 0;
          color: var(--main-light-color);

        }

        textarea:focus {
          outline: 0;
        }
      }

      .negative-description {
        input {
          width: 100%;
          border: none;
          border-bottom: 1px solid var(--main-light-color);
          color: var(--main-light-color);
          padding: 10px 0;
        }

        input:focus {
          outline: 0;
        }
      }
    }

    .wrapper-image {
      position: relative;
      width: 100%;
      height: 200px;
      border: 2px dashed rgba(255, 255, 225, 0.3);

      .upload,
      .pre-show {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;

        input {
          position: absolute;
          opacity: 0;
          width: 100%;
          height: 100%;
          cursor: pointer;
        }

        .label-download {
          width: 24px;
          opacity: 0.3;
        }

        span {
          text-align: center;
          font-size: 13px;
          color: rgba(255, 255, 225, 0.3);
        }
      }

      .pre-show {

        .trash {
          position: absolute;
          top: 10px;
          right: 10px;
          cursor: pointer;
        }

        .pre-show-img {
          width: 150px;
          height: 150px;
          object-fit: cover;
        }
      }
    }
  }
}
</style>
